.height-6 {
    height: 300px;
    margin: auto;
    padding: 10px;
}

.height-6 img {
    height: 100%;
    width: auto !important;
    margin: auto;
}

.pattern-overlay {
    position: absolute;
    top: 0;
    width: 100%;
    opacity: 1;
    bottom: 0;
    /*//background: url(https://hubskills.com/lines-1.png) repeat, url(https://hubskills.com/bg-1.png) !important;*/
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAHCAYAAADEUlfTAAAAG0lEQVQYV2NMKL/ty4ADMIIkF3SqbsYmP+gkAayXGgfe8HOVAAAAAElFTkSuQmCC) repeat !important;
    background-position: left top;
    z-index: 1;
}

.tabs .image.fit {
    height: 100%;
}

.tabs video {
    height: 100%;
    width: 100%;
}



